import { UUID } from "@ilabs/core";
import { ReactElement, useEffect } from "react";
import { IApiPage } from "../ZoningWorkbench/types";
import { Dispatch, IPageState } from "./StateManager/state";

interface IProps {
  children: ReactElement;
  dispatch: Dispatch;
  fetchJobContent: (id: string) => Promise<IApiPage>;
  page: IPageState;
  pageZoneIds: Array<UUID>;
}
export const ZoneLoader = ({ children, dispatch, fetchJobContent, page, pageZoneIds }: IProps) => {
  useEffect(() => {
    const { index, loaded } = page;
    if (loaded) {
      return;
    }
    fetchJobContent(pageZoneIds[index])
      .then(({ predictions }: IApiPage) => {
        dispatch({
          index,
          type: "POPULATE ZONES",
          zones: predictions,
        });
      })
      .catch(err => {
        console.error(err);
      });
  }, [page.index, page.loaded]);
  return children;
};



