import { URI } from "@ilabs/core";
import { ReactElement, useMemo, useReducer } from "react";
import { newPage } from "../helpers";
import { Size } from "../Renderer/types";
import { reducer } from "./reducer";
import { Action, IState, ZoomLevel } from "./state";

export const DEFAULT_INITIAL_STATE: IState = {
  activePage: 0,
  autoShrink: true,
  currentLabel: "text",
  pages: [],
  zoom: ZoomLevel["1x"],
};

interface IChildrenArgs {
  readonly dispatch: (action: Action) => void;
  readonly state: IState;
}

interface IProps {
  readonly children: (args: IChildrenArgs) => ReactElement;
  readonly initialPages: Array<URI>;
  readonly pageDimensions: Array<Size>;
  readonly startingPage: number;
}

export const StateManager = ({ children, initialPages, pageDimensions, startingPage }: IProps) => {
  const initialState: IState = useMemo(
    () => ({
      ...DEFAULT_INITIAL_STATE,
      activePage: startingPage,
      pages: initialPages.map((uri, index) => newPage(index, uri, pageDimensions[index])),
    }),
    [initialPages, startingPage],
  );
  const [state, dispatch] = useReducer(reducer, initialState);
  return children({ dispatch, state });
};



